import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'tz-home-static-content',
    templateUrl: './home-static-content.component.html',
    styleUrls: ['./home-static-content.component.scss', './home-static-content.component.tablet.scss'],
})
export class HomeStaticContentComponent implements OnInit {
    public formGroup = new FormGroup({
        email: new FormControl('', [Validators.pattern(ValidatorPatterns.EMAIL)]),
    });

    constructor(
        private dialogService: DialogService,
        private trazlerAPI: ApiAccessService,
        private translateService: TranslateService
    ) {}

    ngOnInit() {}

    public subscribe(): void {
        const isValidMail = this.formGroup.controls['email'].value && this.formGroup.controls['email'].value !== '';
        if (!isValidMail) {
            return;
        }

        this.trazlerAPI.SubscribeToTz(this.formGroup.controls['email'].value).then(
            (success: any) => {
                this.formGroup.reset();
                this.dialogService.openSnackBar({ message: this.translateService.instant('NOTIFS.SUBSCRIBE_SUCCESS') });
            },
            (failure: any) => {
                console.log('Subscription failure: ', failure);
                if (failure.error) {
                    this.dialogService.openSnackBar({ message: failure.error.message });
                }
            }
        );
    }
}
