import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { GeneralSettings } from '@core/constants/general-settings';
import { DialogService } from '@services/dialog.service';
import { ValidatorPatterns } from '@core/constants/validation-patterns.const';
import { PlatformService } from '@services/platform.service';
import { LandingPageService } from '@services/landing-page.service';
import { SelectedServices } from '@schemas/modules/services/selected-services';
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'tz-plan-my-trip',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss', 'home.component.tablet.scss'],
    //changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PlanMyTripComponent implements OnInit, OnDestroy {
    public editing = true;
    public readonly availableServices = GeneralSettings.availableServices;
    public selectedServices: { [key: string]: boolean } = GeneralSettings.defaultSelectedServices;
    public formGroup = new FormGroup({
        email: new FormControl('', [Validators.pattern(ValidatorPatterns.EMAIL)]),
    });

    private serviceListenerId = -1;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private searchDataService: SearchDataService,
        private listenersService: ListenersService,
        private trazlerAPI: ApiAccessService,
        private dialogService: DialogService,
        public platformService: PlatformService,
        private landingPageService: LandingPageService
    ) {}

    ngOnInit(): void {
        this.initializeHomePage();

        this.landingPageService.landingPageClosed$.pipe(takeUntil(this.destroy$)).subscribe((selectedServices) => {
            this.setServices(selectedServices);
        });
    }

    ngOnDestroy(): void {
        this.listenersService.unregisterListener(this.serviceListenerId);

        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public selectTzService(serviceKey: string): void {
        const s = this.selectedServices;
        const selectedServices = Object.keys(s).filter((serviceIdx) => serviceIdx !== 'activity' && s[serviceIdx]);
        const isLastService = selectedServices.length === 1 && selectedServices[0] === serviceKey;
        if (isLastService || serviceKey === 'activity') {
            return;
        }

        this.selectedServices[serviceKey] = !this.selectedServices[serviceKey];
        // For rental and transfers we reset other service if either one is selected
        if (this.selectedServices[serviceKey] && (serviceKey === 'rentCar' || serviceKey === 'transfer')) {
            this.selectedServices['transfer'] = serviceKey === 'transfer';
            this.selectedServices['rentCar'] = serviceKey === 'rentCar';
        }

        this.searchDataService.updateTzServiceSelection(this.selectedServices);
    }

    private initializeTzServices(): void {
        this.selectedServices = {
            ...GeneralSettings.defaultSelectedServices,
        };
        this.searchDataService.updateTzServiceSelection(this.selectedServices);
    }

    private initializeHomePage(): void {
        this.initializeTzServices();

        this.serviceListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_SERVICE_REINIT,
            this.initializeTzServices,
            this
        );
        this.searchDataService.clear();
        this.searchDataService.cleanUpSearch();
        // tzPassengerService.ResetPax();
        //this.listenersService.NotifyListeners(ListenerId.LISTEN_SERVICE_REINIT);
        this.listenersService.NotifyListeners(ListenerId.LISTEN_EDIT_RENT_CAR, { resetMap: true });

        //this.settingsService.clearDeviceId();
        //this.appConfigService.initDeviceId();
        this.searchDataService.resetSearch$.next();
    }

    private setServices(selectedServices: SelectedServices): void {
        this.selectedServices = selectedServices;
        this.searchDataService.updateTzServiceSelection(this.selectedServices);
    }
}
